.bar-wrapper {
  height: 70vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.bar-item {
  margin: 0 1px;
  width: 5px;
  background-color: #ff7979;
}
.div-bar {
  text-align: center;
}
.div-bar button {
  margin: 50px;
  padding: 20px;
  background: none;
  border: none;
  font-size: 20px;
  background-color: #6c5ce7;
  border-radius: 25px;
  color: white;
  transition: all 0.5s ease-in-out;
}
.div-bar button:hover {
  cursor: pointer;
  transform: translateY(-10px);
  box-shadow: 1px 5px 10px 1px grey;
}
.div-bar .generate-btn
{
    background-color: #f0932b;
}
@media (max-width: 800px) {
  .bar-wrapper {
    height: 60vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .div-bar button {
    margin: 20px;
    padding: 15px;
  }
}
