.nav-wrapper {
  height: 10vh;
  width: 100%;
  background-color: #2d3436;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-wrapper h1 {
  font-size: 50px;
  color: white;
}

@media (max-width: 800px) {
  .nav-wrapper h1 {
    font-size: 30px;
    color: white;
  }
}
